	import md5 from '../../common/md5.js';
export default {
	
	data() {
		return {
			myPhone: '15130134935',
			myPwd: '123456'
		};
	},
	onLoad() {
		// var _this = this;
		// uni.getStorage({
		// 	key: 'lastaccount',
		// 	success: function(res) {
		// 		console.log('getStoragelastaccount' + res.data);
		// 		_this.myPhone = res.data;
