	},

	methods: {
		clickLogin() {
			if (this.myPhone.length == 0) {
				uni.showToast({
					icon: null,
					title: '请输入手机号'
				});
				return;
			}

			if (this.myPwd.length == 0) {
				uni.showToast({
					icon: null,
					title: '请输入密码'
				});
				return;
			}
			var md5Pwd = md5(this.myPwd).substr(8,16);
			console.log(md5Pwd);
			this.requestFromServer({
				url:'/index/common/login',
				data:{
					userName:this.myPhone,
					userPass:md5Pwd
				}
			}).then((res)=>{
				console.log(res);
						// 本地存入 同步
						if (res.status == 200) {
							try {
								uni.setStorageSync('userInfo', res.data);
							} catch (e) {
								console.log(e);
							}
							uni.navigateBack({});
						}
			})
			
			
		},
		
		forget() {
			uni.navigateTo({
				url: './find'
			});
		},
		clickRegist() {
			uni.navigateTo({
				url: './regist'
			});
		},
		wxLogin(){
			uni.login({
				provider:'weixin',
				success: (res) => {
					console.log(res);
					// this.requestFromServer({
					// 	url:'/index/common/loginWechat',
